.Links {
    width: 100%;
    height: 100%;
    text-align: center;
    overflow-y: auto;
}

.CitationBody {
    font-family: "source-serif-pro", Georgia, Courier, serif;
    text-align: center;
    line-height: 2.5;
    width: 50vw;
    margin: auto;
}

.CitationSource {
    text-align: left;
    text-indent: -25px;
    overflow-wrap: break-word;
    padding-left: 25px;
    width: 50vw;
}