@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.Slide {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    width: 85vw;
    height: 60vh;
}

.SlideText {
    -webkit-flex: 1 1;
            flex: 1 1;
    font-family: 'Source Sans Pro', sans-serif;
    font-size: calc(2vmin + 5px);
    line-height: 1.5em;
    overflow-y: auto;
}

.SlideTextContainer {
    padding: 1.5em;
}

.SlideLink {
    -webkit-flex: 1 1;
            flex: 1 1;
    margin: auto;
    height: 100%;
    width: 100%;
    overflow-y: auto;
    overflow-x: hidden;
}

.SlideLinkContainer {
    display: grid;
    place-items: center;
    height: 100%;
    width: 100%;
    padding-right: 10px;
}

.MultiImage {
    display: grid;
    place-items: center;
}
.Slideshow {
    display: grid;
    place-items: center;
    width: 100%;
    height: 100%;
}

.Carousel {
    box-shadow: 5px 10px 8px 5px #c2c2c2;
    width: 85vw;
    height: 60vh;
}

.Date {
    width: 8vw;
    height: 6vh;
    display: grid;
    place-items: center;
    font-family: 'Roboto', sans-serif;
    font-size: 1.75vmin;
    text-align: center;
    border: 1px solid grey;
}

.DateContainer {
    font-size: 1.75vmin;
}
.Home {
    width: 100%;
    height: 100%;
}

.Fiscal {
    width: 100%;
    height: 100%;
}



.Monetary {
    width: 100%;
    height: 100%;
}



.Links {
    width: 100%;
    height: 100%;
    text-align: center;
    overflow-y: auto;
}

.CitationBody {
    font-family: "source-serif-pro", Georgia, Courier, serif;
    text-align: center;
    line-height: 2.5;
    width: 50vw;
    margin: auto;
}

.CitationSource {
    text-align: left;
    text-indent: -25px;
    overflow-wrap: break-word;
    padding-left: 25px;
    width: 50vw;
}
.NavBar {
        height: 100%;
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: row;
                flex-direction: row;
        -webkit-align-items: center;
                align-items: center;
        -webkit-justify-content: left;
                justify-content: left;
}

.NavBarButton {
        display: grid;
        place-items: center;
        -webkit-flex: 1 1;
                flex: 1 1;
        width: 100%;
        max-width: 400px;
        height: 100%;
        font-size: calc(0.5vh + 2vmin);
        text-decoration: none;
        color: white;
}

.NavBarButton:hover {
        background-color: #373d48;
}
.Header {
    background-color: #282c34;
    color: white;
    width: 100vw;
    height: 10vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.HeaderText {
    padding: 5px 2vw 10px;
    border-bottom: 1px solid #404653;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.HeaderTitle {
    font-size: calc(1vh + 1.5vmin);
}

.HeaderSubtitle {
    font-size: calc(1vh + 1vmin);
    display: grid;
    place-items: center;
}
body {
    margin: 0;
    overflow-y: hidden;
    overflow-x: hidden;
}

.main {
    height: 100vh;
    width: 100vw;
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.content {
    height: 90vh;
}

/* width */
::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1; 
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #888; 
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555; 
  }
